.App {
    text-align: center;
  }


.navigation-container {
	width: 100%;
	height: 50px;
	top: 0;
	position: fixed;
	display: flex;
	justify-content: center;
	background-color: #34495e;
	z-index: 2;
  }
  
.navigation {
	display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
	height: 50px;
	background-color: #34495e;
	border-radius: 8px;
	font-size: 0;
	z-index: 2;
}

.navigation .item {
	line-height: 50px;
	height: 100%;
	font-size: 15px;
	display: inline-block;
	position: relative;
	z-index: 1;
	text-decoration: none;
	text-transform: uppercase;
	text-align: center;
	color: white;
	cursor: pointer;
}

.navigation .animation {
	position: absolute;
	height: 100%;
	top: 0;
	z-index: 0;
	transition: all 0.5s ease 0s;
	border-radius: 8px;
}
.item:nth-child(1) {
	width: 100px;
}
.item:nth-child(2) {
	width: 110px;
}
.item:nth-child(3) {
	width: 100px;
}
/* a:nth-child(4) {
	width: 160px;
}
a:nth-child(5) {
	width: 120px;
} */
.navigation .start-home, .item:nth-child(1):hover~.animation {
	width: 100px;
	left: 0;
	background-color: #1abc9c;
}
.navigation .start-projects, .item:nth-child(2):hover~.animation {
	width: 110px;
	left: 100px;
	background-color: #3498db; 
}
.navigation .start-contact, .item:nth-child(3):hover~.animation {
	width: 100px;
	left: 210px;
	background-color: #e74c3c;
}
