.about-container {
    display: flex;
    justify-content: center;
    height: 110vh;
    width: 100%;
    margin-bottom: 220px;
    padding-top: 1%;
    z-index: 3;
}


.about-1 {
    color:#021D44;
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 60px;
    background: #3498db;
   
}

.about-1-text {
    position: sticky;
    position: -webkit-sticky;
    top: 200px;
    padding-right: 2%;
    padding-bottom: 5%;
    font-size: 1.5vw;
    text-align: end;
    font-weight: bolder;
    font-family: 'Quicksand', sans-serif;
}

.about-1-text:hover {
    transform: translate(0,-100px);
    transition: transform 1s;
}

.about-2 {
    width: 50%;
    height: 100%;
    color: white; 
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-top: 60px;
    font-family: 'Open Sans', sans-serif;
    background: #023278;
    
}

.about-2-text {
    position: sticky;
    position: -webkit-sticky;
    top: 200px;
    padding-bottom: 5%;
    font-size: 1.2vw;
    padding-left: 2%;
    text-align: start;
    font-family: 'Quicksand', sans-serif;
}

.about-2-text:hover {
    transform: translate(0,-100px);
    transition: transform 1s;
}


